.tags-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.tag-element {
    flex: 1;
    border-style: solid;
    border-color: #305656;
    border-width: thin;
    cursor: pointer;
    padding: 5px;
}

.tag-element.chosen {
    background-color: #35f9f9;
    color: black;
}

.tag-element.chosen-already {
    background-color: #ff7600;
    color: black;
}


.instructions-text {
    clear: both;
    font-size: 16pt;
}

.tags-navigator {
    display: flex;
}
